.homepage{
    width: calc(100% - 600px);
    margin: 0 auto;
    margin-top: 1rem;
    display: flex; 
    position: relative;
    overflow: hidden;
    background: #fff;

}

.homepage_bg_color{
    background: purple;
    padding-top: 1rem;
}
p.homepage_detail_content {
    margin-top: 1.4rem;
}
.left_side { 
    flex: .3 1;
    padding: 1rem;
    background: #dfdada;
    /* background: linear-gradient(to right, #9f8f8f ,#9b5c5c 50%);  */
   /* background:repeating-radial-gradient( circle at 107px -63px, #705c5c ,#ac9090 113px); */
}
.right_side { 
    flex: .7 1; 
}
.homepage_img {
    height: 234px;
    display: flex;
    align-items: center;
}
.homepage_img img {
    width: 63%;
    display: flex; 
    margin: 0 auto;
}
.homepage_detail {
    height: 240px;
    display: flex;
    flex-direction: column;
    padding: 0 1.7rem;
    background:#dfdada;
    justify-content: center;
    /* background: linear-gradient(to top, #9f8f8f ,#9b5c5c 50%); */
    /* background: repeating-radial-gradient( circle at 0 0, #705c5c ,#ac9090 113px); */
}
.homepage_detail h4 {
    font-size: 2rem;
    margin: 0;
    letter-spacing: 1px;
}
.homepage_detail_profile {
    font-size: .9rem;
    letter-spacing: 1.9px;
    font-weight: 600;
}

.skill {
    width: 47%;
    float: left;
    text-align: center; 
    margin-bottom: 10px; 
    border-radius: 10px;
    background: #fff;
}
.skill:nth-child(odd) {
    margin-left: 5px;
}
.skill:nth-child(even) {
    margin-right: 5px;
}
.homepage_skill, .homepage_hobbis {
    display: flex;
}
.homepage_media{
    display: none;
}

.hobies{width: 100%;}
.ak {
    width: 100%;
}
.Contact_heading p {
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 700;
    text-align: right;
    margin: 0;
    letter-spacing: 1.9px;
    position: relative;
    
    margin-top: 1rem;
    display: inline;
}
.Contact_heading {
    text-align: right;
    margin-bottom: 1.9rem;
    margin-top: 1.9rem;

}
.detail {
    display: flex;
    padding-bottom: 10px;
}
.detail_icon svg {
    padding: 10px;
    background: white;
    border-radius: 10px;
    margin-right: 10px;
}

.detail_data {
    width: 100%;
    align-items: center;
    display: flex;
}

.Contact_heading p:after {
    content: "";
    position: absolute;
    width: 54%;
    right: 0;
    height: 1.9px;
    background: black;
    bottom: -11px;
}
.new_detail {
    padding: 18px 7px;
}
.new_detail i {
    font-size: 1.7rem;
}
p.new_data {
    margin: 0;
    font-size: 13px;
    margin-top: 10px;
}
.hobbies i {
    font-size: 1.4rem;
    margin-bottom: 8px;
}
.hobbies:nth-child(even) {
    margin-left: 6px;
}
.hobbies:nth-child(odd) {
    margin-right: 6px;
}
.hobbies {
    width: calc(100% - 59%);
    float: left;
    text-align: center;  
    margin-bottom: 12px;
    transition: .5s;
    position: relative;
    padding: 37px 4px; 
    margin-bottom: 2rem; 
    background: white; 
    -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%); 
            clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%); 
}
p.view_mode {
    width: 10px;
    height: 10px;
    background: gray;
}
.hobbies:hover { 
    transform: translateY(-10px);
}
.experience_heading p {
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 700;
    text-align: right;
    margin: 0;
    letter-spacing: 1.9px;
    position: relative;
    /* width: 94%; */
    display: inline;
}

.experience_heading {
    text-align: right;
}

.experience_heading p:after {
    width: 40%;
    content: '';
    /* position: relative; */
    height: 2.4px;
    background: #000;
    position: absolute;
    right: 0;
    bottom: -8px;
}
.experience {
    padding: 1.4rem;
    margin-top: 1rem;
}
.e_detail {
    display: flex;
    padding-top: 1.9rem;
}
.e_time { 
    flex: .24 1;
}
.e_details p {
    margin: 0;
}
.e_details {
    flex: .76 1;
    position: relative;
    padding-left: 10px;
}
.e_details:before {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    background: #000;
    border-radius: 50%;
    top: 0;
    left: -1rem;
}
.e_details:after {
    content: '';
    position: absolute;
    width: 2px;
    height: 90%;
    background: #000; 
    top: 0;
    left: -12px;
}
p.e_detail_coName {
    font-size: 1rem;
    text-transform: uppercase;
    letter-spacing: 1.9px;
    font-weight: 700;
    padding-bottom: 3px;
}

p.e_detail_content {
    font-size: .9rem;
    padding-bottom: .9rem;
}
p.e_detail_work {
    margin: 0;
    font-weight: 600;
}
.show_project {
    text-align: right;
    font-size: 1rem;
    font-weight: 900;
    letter-spacing: 1px;
    padding-top: 1rem;
}
.homepage_color {
    position: absolute;
    right: 0; 
    top: 1rem;
}

ul.color_mode {
    display: flex;
    margin: 0;
    height: 45px;
    align-items: center;
    padding: 0;
}

ul.color_mode li {
    margin: 7px;
    width: 1.3rem;
    height: 1.3rem;
    cursor: pointer;
    border-radius: 50%;
    list-style: none;
}

.toggle_style_switcher {
    font-size: 1.4rem;
    line-height: 43px;
}
.toggle_style_switcher {
    height: 45px;
    width: 45px;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: -45px;
    background: #fff;
    text-align: center;
}
.style_switcher {
    background: #fff;
    position: relative;
    transition: all 0.3s ease;
    transform: translateX(100%);
}
.style_switcher h4 {
    display: flex;
    margin: 0;
    align-items: center;
    height: 45px;
    padding-left: 1rem;
    border-bottom: 1px solid;
}
.b_color {
    padding: 1rem 0;
    padding-left: 10px;
}
.body_color h4 {
    height: 24px; 
    border-bottom: 1px solid;
    padding: .4rem 0;
    padding-left: 10px;
}
.body_color {
    background: #fff; 
}
.style_switcher.open{
    transform: translateX(0%);
}
