.music_section {
  font-size: 62.5%;
  font-family: 'jost',"snas-serif";
  height: 100vh;
  display: flex;
  align-items: center;
}

.music_section .music_container {
  box-shadow: 0 1.2rem 3rem rgba(0, 0, 0, 0.2);
          width: 21rem;
          height: 30rem;
          background-color: #fff;
          border-radius: 2rem;
         
          padding: 3rem 1.9rem;
          text-align: center;
          /* margin: 0 auto; */
          box-sizing: border-box;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -45%);
}

.music_section .music_container #title {
  letter-spacing: 3px;
  word-spacing: 4px;
  font-size: 1.3rem;
  color: #171717;
  margin: .7rem 0 0.1rem 0;
  text-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.3);
}

.music_section .music_container #artist {
  color: #cccaca;
  padding: .4rem 0 3.4rem 0;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1.9px;
}

.music_section .music_container .music_img_container img {
  width: 65%;
  height: 64%;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 1rem 3rem 0.5rem rgba(0, 0, 0, 0.4);
}

.music_section .music_container .music_img_container img.anime {
  -webkit-animation: rotateimage 3s linear infinite;
          animation: rotateimage 3s linear infinite;
}

.music_section .music_container .music_controls {
  padding: 1rem 1rem;
  display: flex;
  justify-content: space-between;
}

.music_section .music_container .music_controls i {
  font-size: 1rem;
  padding: 1rem;
}

.music_section .music_container .music_controls i#play {
  background: #000;
  padding: 1rem;
  color: #fff;
  border-radius: 50%;
}

.music_section .music_container .music_controls i#play:hover {
  background: red;
}

.music_section .music_container .progess_bar_container {
  width: 100%;
  height: auto;
  padding: 1rem 0;
}

.music_section .music_container .progess_bar_container .progress_duration_meter {
  display: flex;
  justify-content: space-between;
  font-size: .7rem;
  font-weight: 600;
}

.music_section .music_container .progess_bar_container .progress_div {
  width: 100%;
  height: .4rem;
  box-shadow: 1px 2px rgba(0, 0, 0, 0.07), 0 2px 4px rgba(0, 0, 0, 0.07), 0 4px 8px rgba(0, 0, 0, 0.07), 0 8px 16px rgba(0, 0, 0, 0.07), 0 16px 32px rgba(0, 0, 0, 0.07), 0 32px 64px rgba(0, 0, 0, 0.07);
  border-radius: 4rem;
  position: relative;
  margin-top: .5rem;
  transition: all 1s linear;
  cursor: pointer;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.music_section .music_container .progess_bar_container .progress_div .progress {
  position: absolute;
  width: 64%;
  height: 100%;
  background: #000;
  border-radius: 4rem;
}

@-webkit-keyframes rotateimage {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotateimage {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
