.link_style::before{
    content: "";
    position: absolute;

left: -20px;
top: 24px;
transform: translateY(-50%);
width: 20px;
height:2px ;
background: #0f0;
box-shadow: 5px -8px 0 #0f0,5px 8px 0 #0f0;
transition: width 0.5s,left 0.5s, height 0.5s, box-shadow 0.5s;
transition-delay: 1s, 0.5s, 0s, 0s;
}
.link_style:hover::before{
    width: 60%;
    height: 100%;
    left: -2px;
    box-shadow: 5px 0 0 #0f0, 5px 0 0 #0f0;
    transition-delay: 0s,0.5s,1s,1s;
}
.link_style::after{
    content: "";
    position: absolute;

right: -20px;
top: 24px;
transform: translateY(-50%);
width: 20px;
height:2px ;
background: #0f0;
box-shadow: -5px -8px 0 #0f0,-5px 8px 0 #0f0;
transition: width 0.5s,right 0.5s, height 0.5s, box-shadow 0.5s;
transition-delay: 1s, 0.5s, 0s, 0s;
}
.link_style:hover::after{
    width: 60%;
    height: 100%;
    right: 0;
    box-shadow: -5px 0 0 #0f0, -5px 0 0 #0f0;
